  'Èô™‰º¥Ë¥≠Áâ©': 3,
  'Èô™ÂêåËßÇÂΩ±': 4,
  'Á§º‰ª™Ê®°Áâπ': 5,
  'Êé¢Â∫ó‰ΩìÈ™å': 6,
  'Èô™ËØäÊúçÂä°': 7,
  'Èô™‰º¥ËÅäÂ§©': 8
}

// Ëé∑ÂèñÊäÄËÉΩÂØπÂ∫îÁöÑemoji
const getSkillEmoji = (skill) => {
  const emojiMap = {
    'Èô™ÊãçÂÜôÁúü': 'üì∏',
    'Èô™Êä§Â∞±Âåª': 'üè•',
    'Èô™‰º¥Ë¥≠Áâ©': 'üõçÔ∏è',
    'Èô™ÂêåËßÇÂΩ±': 'üé¨',
    'Á§º‰ª™Ê®°Áâπ': 'üëó',
    'Êé¢Â∫ó‰ΩìÈ™å': 'üç∞',
    'Èô™ËØäÊúçÂä°': 'üíä',
    'Èô™‰º¥ËÅäÂ§©': 'üí¨'
  }
  return emojiMap[skill] || 'üíù'
}

// ÈÄâÊã©ÊÄßÂà´
