    success: (res) => {
      const cities = ['Ê∑±Âú≥Â∏Ç', 'ÂπøÂ∑ûÂ∏Ç', '‰∏äÊµ∑Â∏Ç', 'Âåó‰∫¨Â∏Ç', 'Êù≠Â∑ûÂ∏Ç', 'Âçó‰∫¨Â∏Ç', 'ËãèÂ∑ûÂ∏Ç', 'ÊàêÈÉΩÂ∏Ç', 'ÈáçÂ∫ÜÂ∏Ç', 'Ê≠¶Ê±âÂ∏Ç', 'Ë•øÂÆâÂ∏Ç']
      formData.city = cities[res.tapIndex]
    }
  })
}

// Ê∑ªÂä†ÁÖßÁâá
const addPhoto = () => {
  uni.chooseImage({
    count: 6 - photos.value.length,
    sizeType: ['compressed'],
