      title: 'Âà∑Êñ∞ÊàêÂäü',
      icon: 'success',
      duration: 1500
    })
  } catch (error) {
    uni.showToast({
      title: 'Âà∑Êñ∞Â§±Ë¥•',
      icon: 'none',
      duration: 1500
    })
  } finally {
    isRefreshing.value = false
  }
}

const onRefreshRestore = () => {
  isRefreshing.value = false
}

// Êèê‰∫§Áî≥ËØ∑
const submitApplication = async () => {
  // Èò≤ÈáçÂ§çÊèê‰∫§
  if (isSubmitting.value) {
    return
  }

  // Ë°®ÂçïÈ™åËØÅ
  if (!formData.nickname.trim()) {
    uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÊòµÁß∞', icon: 'none' })
    return
  }
  
  if (formData.nickname.length > 50) {
    uni.showToast({ title: 'ÊòµÁß∞‰∏çËÉΩË∂ÖËøá50‰∏™Â≠óÁ¨¶', icon: 'none' })
    return
