  if (!formData.gender) {
    uni.showToast({ title: 'ËØ∑ÈÄâÊã©ÊÄßÂà´', icon: 'none' })
    return
  }
  
  const age = parseInt(formData.age)
  const height = parseInt(formData.height)
  const weight = parseInt(formData.weight)
  
  if (!age || age < 18 || age > 65) {
    uni.showToast({ title: 'Âπ¥ÈæÑÂøÖÈ°ªÂú®18-65Â≤Å‰πãÈó¥', icon: 'none' })
    return
  }
  
  if (!height || height < 100 || height > 250) {
    uni.showToast({ title: 'Ë∫´È´òÂøÖÈ°ªÂú®100-250cm‰πãÈó¥', icon: 'none' })
    return
  }
  
  if (!weight || weight < 30 || weight > 200) {
    uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÂêàÁêÜÁöÑ‰ΩìÈáç', icon: 'none' })
    return
  }
  
  if (!formData.city.trim()) {
    uni.showToast({ title: 'ËØ∑ÈÄâÊã©ÂüéÂ∏Ç', icon: 'none' })
    return
  }
  
  if (photos.value.length === 0) {
    uni.showToast({ title: 'ËØ∑‰∏ä‰º†Ëá≥Â∞ë‰∏ÄÂº†ÁîüÊ¥ªÁÖß', icon: 'none' })
    return
