  if (photos.value.length > 9) {
    uni.showToast({ title: 'ÊúÄÂ§öÂè™ËÉΩ‰∏ä‰º†9Âº†ÁÖßÁâá', icon: 'none' })
    return
  }
  
  if (selectedSkills.value.length === 0) {
    uni.showToast({ title: 'ËØ∑Ëá≥Â∞ëÈÄâÊã©‰∏ÄÈ°πÊúçÂä°ÊäÄËÉΩ', icon: 'none' })
    return
  }
  
  // Êèê‰∫§Á°ÆËÆ§
  uni.showModal({
    title: 'Á°ÆËÆ§Êèê‰∫§',
    content: 'Á°ÆÂÆöË¶ÅÊèê‰∫§ÂÖ•È©ªÁî≥ËØ∑ÂêóÔºüÊèê‰∫§ÂêéÂ∞ÜËøõÂÖ•ÂÆ°Ê†∏ÊµÅÁ®ã„ÄÇ',
    success: async (res) => {
