// ÊâßË°åÊèê‰∫§
const doSubmit = async () => {
  isSubmitting.value = true
  
  try {
    // ÊûÑÂª∫Á¨¶ÂêàÊé•Âè£Ë¶ÅÊ±ÇÁöÑÊï∞ÊçÆÊ†ºÂºè
    const submitData = {
      nickname: formData.nickname.trim(),
      gender: formData.gender === 'male' ? 'Áî∑' : 'Â•≥',
      age: parseInt(formData.age),
      height: parseInt(formData.height),
      city: formData.city,
      photos: photos.value,
 
      services: selectedSkills.value.map(skill => ({
        service_id: skillToServiceMap[skill],
        level: 1 // ËÆ§ËØÅÁ≠âÁ∫ßÂõ∫ÂÆö‰∏∫1
      }))
    }
    
    console.log('Êèê‰∫§Êï∞ÊçÆ:', submitData)
    
    // Ë∞ÉÁî®Êé•Âè£
    const response = await createCompanionApplication(submitData)
    
    console.log('Êé•Âè£ÂìçÂ∫î:', response)
    
    // Âà§Êñ≠Êé•Âè£ÊòØÂê¶ÊàêÂäü - ÈúÄË¶Å response.data.code == 0
    if (response && response.data && response.data.code === 0) {
      // Êèê‰∫§ÊàêÂäü
      uni.showModal({
        title: 'Áî≥ËØ∑Êèê‰∫§ÊàêÂäü üéâ',
        content: 'ÊÇ®ÁöÑÂèã‰º¥ÂÖ•È©ªÁî≥ËØ∑Â∑≤ÊàêÂäüÊèê‰∫§ÔºÅÊàë‰ª¨‰ºöÂú®3-5‰∏™Â∑•‰ΩúÊó•ÂÜÖÂÆåÊàêÂÆ°Ê†∏ÔºåËØ∑ËÄêÂøÉÁ≠âÂæÖ„ÄÇÂÆ°Ê†∏ÁªìÊûúÂ∞ÜÈÄöËøáÊ∂àÊÅØÈÄöÁü•ÊÇ®„ÄÇ',
        showCancel: false,
        confirmText: 'ÊàëÁü•ÈÅì‰∫Ü',
        success: () => {
          // ËøîÂõû‰∏ä‰∏ÄÈ°µ
          uni.navigateBack()
        }
      })
    } else {
      // Êé•Âè£ËøîÂõûÂ§±Ë¥•
      const errorMsg = (response && response.data && response.data.message) || 'Êèê‰∫§Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
      
      uni.showModal({
        title: 'Êèê‰∫§Â§±Ë¥•',
        content: errorMsg,
        showCancel: false,
        confirmText: 'ÊàëÁü•ÈÅì‰∫Ü'
      })
    }
    
  } catch (error) {
    console.error('Êèê‰∫§Â§±Ë¥•:', error)
    
    // Ê†πÊçÆÈîôËØØÁ±ªÂûãÊòæÁ§∫‰∏çÂêåÁöÑÊèêÁ§∫‰ø°ÊÅØ
    let errorMessage = 'Êèê‰∫§Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
    
    if (error && error.message) {
      if (error.message.includes('ÁΩëÁªú')) {
        errorMessage = 'ÁΩëÁªúËøûÊé•ÂºÇÂ∏∏ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúÂêéÈáçËØï'
      } else if (error.message.includes('ÂèÇÊï∞')) {
        errorMessage = 'Êèê‰∫§‰ø°ÊÅØÊúâËØØÔºåËØ∑Ê£ÄÊü•ÂêéÈáçËØï'
      } else if (error.message.includes('ÈáçÂ§ç')) {
        errorMessage = 'ÊÇ®Â∑≤Êèê‰∫§ËøáÁî≥ËØ∑ÔºåËØ∑ÂãøÈáçÂ§çÊèê‰∫§'
      } else {
        errorMessage = error.message
      }
    }
    
    uni.showModal({
      title: 'Êèê‰∫§Â§±Ë¥•',
      content: errorMessage,
      showCancel: false,
      confirmText: 'ÊàëÁü•ÈÅì‰∫Ü'
    })
  } finally {
    isSubmitting.value = false
